body{
  background: url('img/background.jpg') no-repeat;
  background-size: cover;
  color: #FFF;
}

.app{
  text-align: center;
  padding: 5%;
}

.app-title{
  font-size: 26px;
}


/*Profile*/

.profile{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.profile-info{
  margin-left: 10px;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-pack: center;
      justify-content: center;
  background: #FFF;
  border-radius: 10px;
  color: #555;
  padding: 1em
}

.profile-img{
  width: 150px;
  height: 150px;
  -o-object-fit: cover;
     object-fit: cover;
  border: 10px solid #FFF;
  border-radius: 10px;
}

.profile-name{
  font-size: 26px;
}

.profile-followers, .profile-genres{
  font-size: 18px
}


/*Gallery*/
.track{
  display: inline-block;
  width: 220px;
  height: 220px;
  border-radius: 3px;
  text-align: left;
  cursor: pointer;
  margin: 10px;
  position: relative;
}

.track-img{
  width: 220px;
  height: 220px;
  border: 3px solid #FFF;
  border-radius: 3px;
  -o-object-fit: cover;
     object-fit: cover;
  position: absolute;
}

.track-text, .artist-name{
  background: #000;
  color: #FFF;
  opacity: 0.75;
  width: 214px;
  text-align: center;
  position: absolute;
  margin-left: 3px;
  bottom: 0;
  padding: 10px;
}

.track-play{
  position: absolute;
  width: 220px;
  height: 220px;
}

.track-play-inner{
  position: absolute;
  text-align: center;
  border-radius: 30px;
  font-size: 25px;
  height: 60px;
  width: 60px;
  background: #000;
  color: #FFF;
  padding-top: 13px;
  margin-left: 80px;
  margin-top: 80px;
  opacity: 0;
  -webkit-transition: opacity .1s;
  -o-transition: opacity .1s;
  transition: opacity .1s
}

.track-play-inner:hover{
  opacity: 0.8;
}

.featured{
  background: rgba(0,0,0,0.5);
  min-height: 300px;
  width: 100%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.artist{
  min-width: 214px;
  min-height: 220px;
  border: 3px solid #FFF;
  display: inline;
  margin: 20px;
  position: relative;
}

.artist-img{
  width: 220px;
  height: 220px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 3px;
}



